<template>
  <div class="spinner" :style="{top: top + 'px', position: position }">
    <div class="one" :style="{width: size + 'px', height: size + 'px', 'background-color': color}"></div>
    <div class="two" :style="{width: size + 'px', height: size + 'px', 'background-color': color}"></div>
    <div class="three" :style="{width: size + 'px', height: size + 'px', 'background-color': color}"></div>
  </div>
</template>

<script>
  export default {
    name: 'spinner',
    props: ['top', 'color', 'size', 'position'],
    data () {}
  }
</script>

<style scoped>
  @keyframes bouncedelay{0%, 80%, 100%{-webkit-transform:scale(0)}40%{-webkit-transform:scale(1)}}
  .spinner{position: fixed; top: 30px; left: 50%; transform: translate(-50%, 0); z-index: 100;}
  .one,.two,.three{ display: inline-block; width: 18px; height: 18px; border-radius: 100%; background-color: #ccc; animation: bouncedelay 1.4s infinite ease-in-out both; }
  .one{animation-delay: -0.32s;}
  .two{animation-delay: -0.16s;}
</style>



